.slide
{
    position: absolute;
    top: 0;
    width: 100vw;
    height: 100vh;
    opacity: 0;
    transform: translate(100%, 0%);
    transition: opacity 0.75s ease-in-out, transform 0.75s ease-in-out;
    transition-delay: 0.75s;
    will-change: opacity, transform;
    z-index: 0;

    &.active
    {
        opacity: 1;
        transform: translate(0, 0);
        transition-delay: 0s !important;
        z-index: 1;
    }

    &:first-child
    {
        left: 0;
    }

    .bloc 
    { 
        position: relative; 
        width: 100%;
        height: 100%;

        &::after 
        { 
            content: ''; 
            position: absolute; 
            left: 0; 
            top: 0; 
            width: 100%; 
            height: 100%; 
            background: #000000; 
            opacity: 0.25; 
        } 
    } 
} 

